<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 提示信息 -->
              <a-form-model-item
                label="报废原因名称"
                prop="scrapReasonName"
                ref="scrapReasonName"
              >
                <a-input
                  v-model="entity.scrapReasonName"
                  placeholder="请输入报废原因名称"
                  name="scrapReasonName"
                  @change="inChange"
                ></a-input>
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <g-collapse headerName="测试">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <!-- 创建人 -->
                <span>admin</span>
              </a-col>
              <a-col :sm="6">
                <!-- 最近修改时间 -->
                <span> 2025-04-20 15:00</span>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  export default {
    name: "CreateOrEditScrapReason",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          scrapReasonName: "",
        },
        rules: {
          scrapReasonName: [
            { required: true, message: "不能为空", trigger: "change" },
            {
              pattern: /^[^\s]+(\s+[^\s]+)*$/,
              trigger: "change",
              message: "报废原因名称不能包含空格",
            },
          ],
        },
      };
    },
  
    mounted() {},
  
    methods: {
      // 初始化
      init() {
        console.log(this.id, "初始化");
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
  
            this.entity = {
              scrapReasonName: "测试",
              description: "描述",
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        // 重置表单绑定的数据源
        this.entity = {
          scrapReasonName: "",
        };
      },
      async save() {
        // 保存 校验
        let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.scrapReasonName);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          });
        }
      },
  
      inChange(e) {
        console.log(e.target.value);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>